                <v-tab href="#tab-1" @click="routeNAME = 'supply-ready-mato-kirim'" v-if="$can('read', 'supply_tayyor_mato_kirim')">
                    Kirim
                </v-tab>

                <v-tab href="#tab-2" @click="routeNAME = 'supply-ready-mato-chiqim'" v-if="$can('read', 'supply_tayyor_mato_chiqim')">
                    Chiqim
                </v-tab>

                <v-tab href="#tab-3" @click="routeNAME = 'supply-ready-mato-mavjud'" v-if="$can('read', 'supply_tayyor_mato_mavjud')">
                    Mavjud tayyor matolar
                </v-tab>
            </v-tabs>
            <v-col cols="auto">
                <div class="d-flex align-center">
                    <!-- <TableFilter :filterMenu="filterMenu"></TableFilter> -->
                    <DatePicker></DatePicker>
                </div>
            </v-col>
        </div>
        <v-divider></v-divider>
        <v-tabs-items v-model="tab" class="tab-items">

