            </v-tab-item>


            <v-tab-item value="tab-2">
                <Outcome v-if="$can('read', 'supply_tayyor_mato_chiqim')"></Outcome>
            </v-tab-item>


            <v-tab-item value="tab-3">
                <ExistedReadyMaterial v-if="$can('read', 'supply_tayyor_mato_mavjud')"></ExistedReadyMaterial>
            </v-tab-item>

        </v-tabs-items>
    </div>
</template>


<script>
import Income from './Income.vue'
import Outcome from './Outcome.vue'
import ExistedReadyMaterial from './ExistedReadyMaterial.vue'
//import TableFilter from '@/components/TableFilter.vue'
import DatePicker from '@/components/DatePicker.vue'

export default {
    components: {
        Income,
        Outcome,
        ExistedReadyMaterial,
        DatePicker
        // TableFilter
    },
    data() {
        return {
            tab: '',
            filterMenu: false,
        }
    },
    computed: {
        routeNAME: {
            get: function() {
                return this.$store.state.main.routeNAME
            },
            set: function(newValue) {
                return this.$store.state.main.routeNAME = newValue
            }
        },
    }
};
</script>


